          ci_company_type:'1',
          ci_company_id:ci_company_id,
          companyIds :this.companyIds,

        } ;
        if(that.sortOrderCol!==''){
          _jsondata_.orderby = that.sortOrderCol;
        }
        //console.info("json:"+JSON.stringify(_jsondata_));
