          that.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          that.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){

            // loading.close();
            that.tableData = res.data.rows;
            that.queryParam.totalRow = res.data.total;

          }else{
            console.info("查询失败");
            // loading.close();

            that.tableData = [];
            that.$alert(res.data.msg, '查询失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  that.$router.replace({path:'/login'});
                }
              }
            });
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          // loading.close();
          that.tableData = [];
          that.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          // loading.close();

          that.tableData = [];
          that.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

