    padding:10px!important;
  }
  .company-card2 /deep/ .el-card__body{
    padding-top:5px!important;
    padding-bottom:5px!important;
    padding-left:20px!important;
    padding-right:20px!important;
  }
  .company-formitem /deep/ .el-form-item {margin-bottom: 3px!important;}

  .new-table-fixed-right /deep/ .el-table__fixed-right { height: 100% !important;  }
  .new-table-fixed /deep/ .el-table__fixed { height: 100% !important;}

  #el-cascader {width: 100%;}

</style>

